<script>
import { ElMessage } from 'element-plus'
import { ref} from 'vue'
import {
  getCourseInfo,
  courseSubmit,
} from "@/service/genServ.js";
export default {
  name: "CourseEdit",
  data() {
    return {
      options: ref([
        {
          courseday: '星期一',
          label: '星期一',
        },
        {
          courseday: '星期二',
          label: '星期二',
        },
        {
          courseday: '星期三',
          label: '星期三',
        },
        {
          courseday: '星期四',
          label: '星期四',
        },
        {
          courseday: '星期五',
          label: '星期五',
        },
        {
          courseday: '星期六',
          label: '星期六',
        },
        {
          courseday: '星期天',
          label: '星期天',
        },
      ]),
      options1: ref([
        {
          courseorder: '第一节',
          label: '第一节',
        },
        {
          courseorder: '第二节',
          label: '第二节',
        },
        {
          courseorder: '第三节',
          label: '第三节',
        },
        {
          courseorder: '第四节',
          label: '第四节',
        },
        {
          courseorder: '第五节',
          label: '第五节',
        },
      ]),
      form: {
        courseId: null,
        courseNum: "",
        courseName: "",
        courseteacher:"",
        courseinfo:"",
        courseday:ref(''),
        courseorder:ref(''),
        coursescore:"",
        courseselectend:""
      },
    }
  },
  created() {
    this.form.courseId = this.$route.query.courseId;
    this.doQuery();
  },
  methods: {
    doQuery() {
      getCourseInfo({ courseId: this.form.courseId }).then(
        (res) => {
          this.form = res.data;
        }
      );
    },

    submit() {
      courseSubmit({
        form: this.form,
      }).then((res) => {
         ElMessage({
          message: '提交成功！',
          type: 'success',
        })
        this.form.courseId = res.data;
      });
      
    },
    
  },
};
</script>

